import { useLang } from '@app/services/config/hooks'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { Button, Text } from '@instacart/cocktail'
import { responsive, spacing } from '@instacart/ids-core'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import AssetImage from '../../common/AssetImage'

export type ModalProps = {
  onContinue: () => void
}

const useStyles = createStyles(theme => ({
  body: {
    marginBottom: spacing.s24,
  },
  brandLogo: {
    height: 35,
    width: '100%',
  },
  button: {
    fontWeight: 600,
  },
  container: {
    height: '100%',
  },
  header: {
    alignItems: 'center',
    backgroundColor: theme.colors.brandPrimaryRegular,
    display: 'flex',
    height: 46,
  },
  mainContent: {
    backgroundColor: theme.colors.systemGrayscale00,
    height: '100%',
    margin: '20% auto',
    padding: spacing.s32,
    width: '450px',
    [responsive.only('c')]: {
      margin: 0,
      width: 'auto',
    },
  },
  title: {
    fontSize: 22,
    marginBottom: spacing.s8,
  },
}))

const RxRedirectErrorModal = ({ onContinue }: ModalProps) => {
  const styles = useStyles()
  const lang = useLang()
  const isMobile = useIsMobile()

  return (
    <div css={styles.container}>
      {isMobile && (
        <div css={styles.header}>
          <AssetImage css={styles.brandLogo} src={'images/header-logo.svg'} alt={lang.clientName} />
        </div>
      )}
      <div css={styles.mainContent}>
        <Text css={styles.title} bold variant="h1" align="center">
          {lang.rx.errorModal?.title}
        </Text>
        <Text css={styles.body} variant="body" align="center">
          {lang.rx.errorModal?.body}
        </Text>
        <Button css={styles.button} fullWidth variant="link" onClick={() => onContinue()}>
          {lang.rx.errorModal?.buttonText}
        </Button>
      </div>
    </div>
  )
}

export default RxRedirectErrorModal
